<template>
  <div class="region">
    <h1 class="region_title">{{ regionName }}</h1>
    <div class="region_content">
      <h3 class="region_subtitle">
        Рейтинг по задачам
      </h3>
      <el-table :data="tasksTableData" style="width: 100%">
        <el-table-column prop="year" label="Год" width="180"> </el-table-column>
        <el-table-column
          prop="setTasks"
          label="Задач поставлено"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="setSelected"
          label="Задач отобрано"
          align="center"
        >
        </el-table-column>
        <el-table-column prop="setSolved" label="Задач решено" align="center">
          <template slot-scope="scope">
            <a
              v-for="item of scope.row.setSolved"
              :key="item"
              :href="item.link"
              class="sol-link"
              target="_blank"
              >{{ item.name }}</a
            >
          </template>
        </el-table-column>
      </el-table>
      <h3 class="region_subtitle">
        Рейтинг по внедрению
      </h3>
      <el-table :data="injectTableData" style="width: 100%">
        <el-table-column prop="setTasks" label="Задач внедрено">
          <template slot-scope="scope">
            <a
              v-for="item of scope.row.solutions"
              :key="item"
              :href="item.link"
              class="sol-link"
              target="_blank"
              >{{ item.name }}</a
            >
          </template>
        </el-table-column>
        <el-table-column
          prop="setSelected"
          label="Цифровой наставник"
          align="center"
        >
          <template slot-scope="scope">
            <i v-if="scope.row.mentor" class="el-icon-success"></i>
          </template>
        </el-table-column>
        <el-table-column
          prop="setSolved"
          label="Разместил цифровую практику на СМАРТЕКЕ"
          align="center"
          ><template slot-scope="scope">
            <a
              v-if="scope.row.smartekaLink"
              :href="scope.row.smartekaLink"
              class="sol-link"
              target="_blank"
              >ссылка</a
            >
          </template>
        </el-table-column>
      </el-table>
      <h3 class="region_subtitle">
        Рейтинг по сообществам
      </h3>
      <el-table :data="communityTableData" style="width: 100%">
        <el-table-column prop="year" label="Год" width="180"> </el-table-column>
        <el-table-column
          prop="setTasks"
          label="Data-хакатоны"
          width="180"
          align="center"
        >
          <template slot-scope="scope">
            <template v-if="scope.row.year < 2020">
              <i v-if="scope.row.dataHack" class="el-icon-success"></i>
            </template>
            <template v-if="scope.row.year >= 2020">
              {{ scope.row.dataHack }}
            </template>
          </template>
        </el-table-column>
        <el-table-column prop="setSelected" label="Data-дебаты" align="center">
          <template slot-scope="scope">
            <i v-if="scope.row.dataDebates" class="el-icon-success"></i>
            <i v-if="!scope.row.dataDebates" class="el-icon-error"></i>
          </template>
        </el-table-column>
        <el-table-column
          prop="setSolved"
          label="Команды победители из региона"
          align="center"
        >
          <template slot-scope="scope">
            <a
              v-for="item of scope.row.winners"
              :key="item"
              :href="item.link"
              class="sol-link"
              target="_blank"
              >{{ item.rank }}. {{ item.name }}</a
            >
          </template>
        </el-table-column>
      </el-table>
      <br />
      <br />
      <button type="button" class="set-button">
        Поставить задачу на 2021 год
      </button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "App",
  props: ["regionName"],
  data() {
    return {
      regionData: null,
    };
  },
  created() {
    this.loadData();
  },
  computed: {
    tasksTableData() {
      return this.regionData
        ? [
            {
              year: 2020,
              setTasks: this.regionData["Задач поставлено"],
              setSelected: this.regionData["Задач отобрано"],
              setSolved: [
                { name: "Ссылка 1", link: "Ссылка на решение" },
                { name: "Ссылка 2", link: "Ссылка на решение" },
                { name: "Ссылка 3", link: "Ссылка на решение" },
              ],
            },
          ]
        : [];
    },
    injectTableData() {
      return this.regionData
        ? [
            {
              solutions: [
                {
                  name: "Решение 1",
                  link: "link1",
                },
              ],
              mentor: true,
              smartekaLink: "link",
            },
            {
              solutions: [
                {
                  name: "Решение 2",
                  link: "link1",
                },
              ],
              mentor: false,
              smartekaLink: "",
            },
          ]
        : [];
    },
    communityTableData() {
      return this.regionData
        ? [
            {
              year: 2019,
              dataHack: 0,
              dataDebates: false,
              winners: [
                {
                  name: "Team1",
                  link: "link",
                  rank: 1,
                },
              ],
            },
            {
              year: 2020,
              dataHack: this.regionData["Data-хакатоны"],
              dataDebates: true,
              winners: [
                {
                  name: "Team1",
                  link: "link",
                  rank: 1,
                },
                {
                  name: "Team2",
                  link: "link",
                  rank: 2,
                },
              ],
            },
          ]
        : [];
    },
  },
  methods: {
    async loadData(regionName) {
      const { data } = await axios("./data/regions.json");
      this.regionData = data.find((e) => e["Субъект РФ"] === this.regionName);
    },
  },
};
</script>

