  &_subtitle {
    font-size: 20px;
    line-height: 1.35;
    color: #000;
    margin-top: 30px;
  }
}
.sol-link {
  display: block;
  width: 100%;
  text-decoration: none;
  color: #505aea;
}
.set-button {
  background: #505aea;
  padding: 20px;
  border: none;
  color: #fff;
  cursor: pointer;
  &:hover {
    background: rgba(#505aea, 0.8);
  }
}
</style>
